/* stylelint-disable */
::placeholder {
  color: #666
}

html, body {
  margin: 0;
  padding: 0;
}

ul, li {
  display: block;
  padding: 0;
  margin: 0;
}

li {
  list-style: none;
}

body {
  font-size: 16px;
  min-width: 320px;
  position: relative;
  font-family: $arch-black;
  font-weight: 400;
}

input, textarea {
  border: #666 1px solid;
  outline: none;

  &:focus:required:invalid {
    border-color: red
  }

  &:required:valid {
    border-color: green
  }
}





